
import { defineComponent, computed } from 'vue'

export default defineComponent({
    name: 'AppModal',

    props: {
        modelValue: {
            type: Boolean,
            required: true,
        },
    },
    emits: ['update:modelValue'],

    setup(props, { emit }) {
        return {
            toggle: computed({
                get() {
                    return props.modelValue
                },
                set(value) {
                    emit('update:modelValue', value)
                },
            }),
        }
    },
})
